
.ratings .ratingList {
  padding: 0 18px;
}
.ratings .ratingList .itemList {
  padding: 18px 0;
  border-bottom: 1px solid rgba(7, 17, 27, .1);
  position: relative;
}
.ratings .ratingList .itemList .time{
    font-size: 10px;
    font-weight: 200;
    line-height: 12px;
    color:rgb(147,153,159);
    position: absolute;
    right: 18px;
    top: 18px;
}
.ratings .ratingList .itemList .avatar img {
  border-radius: 50%;
}
.ratings .ratingList .itemList .avatar {
  display: inline-block;
  position: absolute;
  top: 18px;
  left: 18px;
}
.ratings .ratingList .itemList .info {
  display: inline-block;
  padding-left: 58px;
}
.ratings .ratingList .itemList .info .name {
  font-size: 10px;
  line-height: 12px;
  color: rgb(7, 17, 27);
  vertical-align: top;
}
.ratings .ratingList .itemList .info .starwrap {
  display: inline-block;
  vertical-align: top;
  margin-top: -2px;
  margin-right: 6px;
}
.ratings .ratingList .itemList .info .star {
  display: inline-block;
}
.ratings .ratingList .itemList .info .star .star-item {
  height: 6px;
  width: 6px;
  margin-right: 2px;
}
.ratings .ratingList .itemList .info .delivery {
  font-size: 10px;
  font-weight: 200;
  color: rgb(147, 153, 159);
  line-height: 12px;
}
.ratings .ratingList .itemList .content {
  font-size: 12px;
  line-height: 18px;
  color: rgb(7, 17, 27);
  padding: 8px 36px 8px 58px;
}
.ratings .ratingList .itemList .recommend{
  padding-left: 58px;
}
.ratings .ratingList .itemList .recommend .block{
  display: inline-block;
  border: 1px solid rgba(7,17,27,.1);
  border-radius: 2px;
  background: #FFFFFF;
  font-size: 9px;
  line-height: 16px;
  color: rgb(147,153,159);
  height: 16px;
  padding: 0 6px;
  margin-right: 8px;
}
.ratings .ratingList .itemList .recommend .icon-good{
  background: #FFFFFF;
  color: rgb(0,160,220);
  margin-right: 8px;
}
.ratings {
  position: absolute;
  top: 174px;
  bottom: 0;
  left: 0;
  overflow: hidden;
  width: 100%;
}
.ratings .overview {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 18px 0;
  border-bottom: 1px solid rgba(7, 17, 27, .1);
}
.ratings .overview .overview-left {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 137px;
          flex: 0 0 137px;
  text-align: center;
  border-right: 1px solid rgba(7, 17, 27, .1);
}
.ratings .overview .overview-left .score {
  font-size: 24px;
  color: rgb(255, 153, 0);
  line-height: 28px;
}
.ratings .overview .overview-left .title {
  font-size: 12px;
  color: rgb(7, 17, 27);
  line-height: 12px;
  margin: 8px 0 6px;
}
.ratings .overview .overview-left .rank {
  font-size: 10px;
  line-height: 10px;
  color: rgb(147, 153, 169);
  margin-bottom: 6px;
}
.ratings .overview .overview-right .scoreWrapper {
  font-size: 0;
  line-height: 18px;
  margin-bottom: 8px;
}
.ratings .overview .overview-right .scoreWrapper .name {
  font-size: 12px;
  color: rgb(7, 17, 27);
  line-height: 18px;
  padding-left: 24px;
  padding-right: 12px;
  display: inline-block;
  vertical-align: top;
}
.ratings .overview .overview-right .scoreWrapper .star {
  display: inline-block;
  height: 18px;
  vertical-align: top;
}
.ratings .overview .overview-right .scoreWrapper .star .star-item {
  height: 18px;
  width: 18px;
  margin-right: 3px;
}
.ratings .overview .overview-right .scoreWrapper .scoreOrange {
  font-size: 12px;
  color: rgb(255, 153, 0);
  line-height: 18px;
  vertical-align: top;
  margin-left: 12px;
}
.ratings .overview .overview-right .scoreWrapper .time {
  font-size: 12px;
  color: rgb(147, 153, 159);
  line-height: 18px;
  vertical-align: top;
}



