
.goods{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: absolute;
  top:174px;
  bottom: 46px;
  width:100%;
  overflow: hidden;
}
.goods .menu-wrapper{
  -webkit-box-flex:0;
      -ms-flex:0 0 80px;
          flex:0 0 80px;
  background:#f3f5f7;
}
.goods .menu-wrapper .current{
  position: relative;
  margin-top: -1px;
  z-index: 10;
  background: #fff;
  font-weight: 700;
}
.goods .menu-wrapper .current .text{
  border:none;
}
.goods .menu-wrapper .menu-item{
  display: table;
  height: 54px;
  width: 80px;
  font-size: 12px;
  line-height: 14px;
  color: #333333;
  font-weight: 200;
  box-sizing: border-box;
  padding:0 12px;
}
.goods .menu-wrapper .menu-item .icon{
}
.goods .menu-wrapper .menu-item .text{
  display: table-cell;
  vertical-align: middle;
  border-bottom: 1px solid rgba(7,17,27,.1);
}
.goods .goods-wrapper .title{
  height: 26px;
  line-height: 26px;
  padding-left: 14px;
  border-left:2px solid #d9dde1;
  font-size: 12px;
  color: rgb(147,153,159);
  background: #f3f5f7;
}
.goods .goods-wrapper .foodlist{
  padding:18px;
  position: relative;
}
.goods .goods-wrapper .foodlist .icon{
  margin-right:10px;
  float:left;
}
.goods .goods-wrapper .foodlist .icon img{
  height: 90px;
  width:90px;
}
.goods .goods-wrapper .foodlist .content{
  float: left;
  width: 158px;
}
.goods .goods-wrapper .foodlist .cartcontrol-wrapper{
  position: absolute;
  right:18px;
  bottom: 18px;
}
.goods .goods-wrapper .foodlist .content .name{
  font-size: 14px;
  color:rgb(7,17,27);
  line-height: 14px;
  margin:2px 0 8px 0;
}
.goods .goods-wrapper .foodlist .content .dec,.extra{
  font-size: 10px;
  line-height: 10px;
  margin:8px 0;
  color: rgb(147,153,159);
}
.goods .goods-wrapper .foodlist .content .price .newprice{
  font-size: 14px;
  line-height: 24px;
  color:red;
  font-weight:700;
  margin-right: 8px;
}
.goods .goods-wrapper .foodlist .content .price .oldprice{
  font-size:10px;
  font-weight: 700;
  line-height: 24px;
  color: rgb(147,153,159);
}



