
#App .tab{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 40px;
  line-break: 40px;
}
#App .tab .tab-item{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  border-bottom: 1px solid rgba(7,17,27,0.1);
  line-height: 39px;
}
#App .tab .tab-item a{
   display: block;
   font-size: 14px;
   color: rgb(77,85,93);
}
#App .tab .tab-item .active{
   color: red;
}



