
.shopcart{
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 10;
  width: 100%;
  height: 48px;
  background: #000;
}
.shopcart .content{
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   background: #141d27;
}
.shopcart .content .contentleft{
   -webkit-box-flex: 1;
       -ms-flex: 1;
           flex: 1;
   font-size: 0;
}
.shopcart .content .contentleft .num{
   position: absolute;
   width: 24px;
   height: 16px;
   font-size: 9px;
   font-weight: 700;
   color: rgb(255,255,255);
   line-height: 16px;
   padding: 0 6px;
   background:rgb(240,20,20);
   box-shadow: 0 4px 8px 0 rgba(0,0,0,.4);
   border-radius: 6px;
   text-align: center;
   box-sizing: border-box;
   top: 0;
   left: 26px;
   z-index: 5;
}
.shopcart .content .contentleft .logo-wrapper{
   display: inline-block;
   position: relative;
   margin: 0 18px;
   padding:6px 0;
   width: 56px;
   height: 56px;
   border-radius:50%;
   background: #141D27;
   top: -10px;
}
.shopcart .content .contentleft .logo-wrapper .logo{
   width: 40px;
   height: 40px;
   border-radius:50%;
   background: #2b343c;
   background: url(shopping_cart.svg) 0 0 center;
   background-size: contain;
   position: relative;
   left: 8px;
   top: 0px;
   line-height: 40px;
   text-align: center;
}
.shopcart .content .contentleft .logo-wrapper .logo svg{
   margin-top: 8px;
   color:#FFFFFF;
}
.shopcart .content .contentleft .price{
   display: inline-block;
   vertical-align: top;
   font-size: 16px;
   font-weight: 700;
   line-height: 24px;
   color: rgba(255,255,255,.4);
   padding-right: 12px;
   margin-top:12px;
   box-sizing: border-box;
   border-right: 1px solid rgba(255,255,255,.1);
}
.shopcart .content .contentleft .desc{
   display: inline-block;
   margin: 12px 0  0 12px;
   vertical-align: top;
   line-height: 24px;
   font-size: 10px;
   color: rgba(255,255,255,.4);
   font-weight: 700;
}
.shopcart .content .contentright{
   -webkit-box-flex: 0;
       -ms-flex: 0 0 105px;
           flex: 0 0 105px;
   width: 105px;
}
.shopcart .content .enough{
    background: #00b43c;
    color: #FFFFFF;
}
.shopcart .content .no_enough{
    background: #2b333b;
}
.shopcart .content .contentright .pay{
   font-size: 12px;
   font-weight: 700;
   line-height: 24px;
   color: rgba(255,255,255,.4);
   text-align: center;
   margin-top: 12px;
}
.shopcart .content .enough .pay{
   color: #FFFFFF;
}
.shopcart .ball-container .ball{
   position: fixed;
   left: 32px;
   bottom:22px;
   z-index: 200;
}
.shopcart .assistBack{
   position: absolute;
   left: 0;
   top: -235px;
   height: 500px;
   width: 100%;
   z-index: -10;
   background: #FFFFFF;
}
.shopcart .shopcartList{
  position: absolute;
   left: 0;
   top: -235px;
   z-index: -10;
   width: 100%;
   overflow: hidden;
   background: #FFFFFF;
}
.shopcart .shopcartList .list-header{
   height: 40px;
   line-height: 40px;
   padding: 0 18px;
   background: #F3F5F7;
   border-bottom: 2px solid rgba(7,17,27,.1);
}
.shopcart .shopcartList .list-header .title{
   font-size: 14px;
   font-weight: 200;
   line-height: 40px;
   color: rgb(7,17,27);
   float: left;
}
.shopcart .shopcartList .list-header .empty{
   font-size: 12px;
   line-height: 40px;
   color: rgb(0,160,220);
   float: right;
}
.shopcart .shopcartList .list-content{
   background: #FFFFFF;
   padding: 0 18px;
   overflow: hidden;
   max-height: 217px;
}
.shopcart .shopcartList .list-content .foodlist{
   height: 48px;
   line-height: 48px;
   box-sizing:border-box;
   border-bottom:1px solid rgba(7,17,27,.1);
}
.shopcart .shopcartList .list-content .foodlist .name{
   font-size: 14px;
   color: rgb(7,17,27);
   line-height: 48px;
   vertical-align: top;
   overflow: hidden;
}
.shopcart .shopcartList .list-content .foodlist .wrapper{
   float: right;
}
.shopcart .shopcartList .list-content .foodlist .wrapper .price{
    font-size: 14px;
    line-height: 24px;
    color: rgb(240,20,20);
    font-weight: 700;
    margin-right: 12px;
}
.shopcart .shopcartList .list-content .foodlist .wrapper .price .yuan{
    font-size: 10px;
    font-weight: normal;
}
.shopcart .shopcartList .list-content .foodlist .wrapper .cartControlWrapper{
   display: inline-block;
   height: 48px;
   line-height: 48px;
   vertical-align: top;
}
.shopcart .shopcartList .list-content .foodlist .wrapper .cartControlWrapper .cartcontrol{
   margin-top: 12px;
}
.shopcart .listMask{
   position: fixed;
   top: 0;
   left: 0;
   height: 100%;
   width: 100%;
   z-index:-20;
   background: rgba(7,17,27,.6);
   -webkit-backdrop-filter:blur(10px);
           backdrop-filter:blur(10px);
}
 /*动画*/
.carList-enter{
   opacity: 0;
   -webkit-transform:translateY(100%);
           transform:translateY(100%);
}
.carList-enter-active{
   transition: all .5s;
}
.carList-leave-active{
   opacity: 0;
   -webkit-transform:translateY(100%);
           transform:translateY(100%);
   transition: 1s;
}
.Back-enter{
   opacity: 0;
}
.Back-enter-active{
   transition: all 1.5s;
}
.Mask-enter{
   opacity: 0;
}
.Mask-enter-active{
   opacity: 1;
   transition: all 1s;
}
.Mask-leave-active{
   opacity: 0;
   transition:all .5s;
}



