
@import url("style.css");
.seller {
  position: absolute;
  width: 100%;
  top: 174px;
  bottom: 0px;
  overflow: hidden;
}
.seller .seller-header {
  padding: 18px;
  border-bottom: 1px solid rgba(7, 17, 27, .1);
  position: relative;
}
.seller .seller-header .favourite{
  position: absolute;
  right:18px;
  top: 18px;
  text-align: center;
  width: 30px;
}
.seller .seller-header .favourite .love{
  color: rgb(240,20,20);
  font-size: 24px;
  line-height: 24px;
}
.seller .seller-header .favourite .bad{
  color: rgba(7,17,27,.3);
  font-size: 24px;
  line-height: 24px;
}
.seller .seller-header .favourite .txt{
  font-size: 10px;
  line-height: 10px;
  color: rgb(77,85,93);
  margin-top: 4px;
}
.seller .seller-header .sellerinfo {
  padding-bottom: 18px;
  border-bottom: 1px solid rgba(7, 17, 27, .1);
}
.seller .seller-header .name {
  font-family: "微软雅黑";
  font-size: 16px;
  color: rgb(7, 17, 27);
  line-height: 14px;
  font-weight: 400;
  margin-bottom: 8px;
}
.seller .seller-header .sellerinfo .starwrap {
  display: inline-block;
  height: 18px;
  vertical-align: top;
}
.seller .seller-header .sellerinfo .starwrap .star {
  text-align: left;
}
.seller .seller-header .sellerinfo .starwrap .star .star-item {
  height: 18px;
  width: 18px;
  margin-right: 6px;
}
.seller .seller-header .sellerinfo .rating-count,
.month-count {
  font-size: 10px;
  line-height: 18px;
  color: rgb(77, 85, 93);
  vertical-align: middle;
}
.seller .seller-header .sellerinfo .rating-count {
  margin-right: 12px;
}
.seller .seller-header .numBox {
  padding-top: 18px;
  width: 100%;
}
.seller .seller-header .numBox .block {
  float: left;
  width: 33%;
  text-align: center;
  font-size: 10px;
}
.seller .seller-header .numBox .block .desc {
  font-size: 10px;
  line-height: 10px;
  color: rgb(147, 153, 159);
  margin-bottom: 4px;
}
.seller .seller-header .numBox .block .num {
  font-size: 24px;
  line-height: 25px;
  color: rgb(7, 17, 27);
  font-weight: 200;
}
.seller .seller-header .numBox .block:nth-child(2) {
  border-left: 1px solid rgba(7, 17, 27, .1);
  border-right: 1px solid rgba(7, 17, 27, .1);
}
.seller .activity {
  padding: 18px;
  padding-bottom: 0;
  border-bottom: 1px solid rgba(7, 17, 27, .1);
}
.seller .activity .title {
  font-family: "微软雅黑";
  font-size: 16px;
  color: rgb(7, 17, 27);
  line-height: 14px;
  font-weight: 400;
  margin-bottom: 8px;
}
.seller .activity .text {
  font-size: 12px;
  font-weight: 400;
  color: rgb(240, 20, 20);
  line-height: 24px;
  padding: 0 12px;
}
.seller .activity .Sinfo .Sitem .icon0 {
  background: url(decrease_2@2x.png);
}
.seller .activity .Sinfo .Sitem .icon1 {
  background: url(discount_2@2x.png);
}
.seller .activity .Sinfo .Sitem .icon2 {
  background: url(guarantee_2@2x.png);
}
.seller .activity .Sinfo .Sitem .icon3 {
  background: url(invoice_2@2x.png);
}
.seller .activity .Sinfo .Sitem .icon4 {
  background: url(special_2@2x.png);
}
.seller .activity .Sicon {
  display: inline-block;
  margin-right: 6px;
  height: 16px;
  width: 16px;
  vertical-align: top;
  background-size: contain !important;
}
.seller .activity .Sinfo .Sitem {
  padding: 16px 12px;
  border-top: 1px solid rgba(7, 17, 27, .1);
  font-size: 12px;
  line-height: 16px;
  color: rgb(7, 17, 27);
  font-weight: 200;
  vertical-align: top;
}
.seller .sellerLive {
  padding: 18px;
  border-bottom: 1px solid rgba(7, 17, 27, .1);
}
.seller .sellerLive .title {
  font-family: "微软雅黑";
  font-size: 16px;
  color: rgb(7, 17, 27);
  line-height: 14px;
  font-weight: 400;
  margin-bottom: 8px;
}
.seller .sellerLive .picWrapper {
  width: 100%;
  white-space: normal;
  overflow: hidden;
}
.seller .sellerLive .picBox {
  font-size: 0;
  white-space: normal;
  width:498px;
}
.seller .sellerLive .picBox li {
  display: inline-block;
  margin-right: 6px;
  height: 90px;
  width: 120px;
}
.seller .sellerLive .picBox li:last-child {
  margin: 0;
}
.seller .sellerInfos{
  padding: 18px 18px 0 18px;
}
.seller .sellerInfos .title{
  font-family: "微软雅黑";
  font-size: 16px;
  color: rgb(7, 17, 27);
  line-height: 14px;
  font-weight: 400;
  margin-bottom: 12px;
}
.seller .sellerInfos .itemInfo{
  padding: 16px 12px;
  line-height: 12px;
  font-size: 12px;
  font-weight: 200;
  line-height: 16px;
  color: rgb(7,17,27);
  border-top: 1px solid rgba(7,17,27,.1);
}



