
@import url("../../common/style.css");
.food{
    position: fixed;
    left: 0;
    top: 0;
    bottom: 48px;
    background: #FFFFFF;
    width: 100%;
    height: 94%;
    z-index:5;
    overflow: hidden;
}
.food .food-content{
}
.food .img-header{
    position: relative;
    width: 100%;
    height: 0;
    padding-top:100%;
}
.food .img-header img{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
}
.food .img-header .icon-wrap{
    display: inline-block;
    height: 30px;
    width: 30px;
    position: absolute;
    left: 10px;
    top: 10px;
    background: rgba(7,17,27,.4);
    border-radius: 50%;
}
.food .img-header .icon-back{
    display: inline-block;
    height: 30px;
    width: 30px;
    background: url(back.png) center center no-repeat;
    background-size: contain;
}
.food .food-content .content{
    padding-left: 18px;
    border-bottom: 1px solid rgba(7,17,27,.1);
    position: relative;
}
.food .food-content .content .title{
    font-size:14px;
    font-weight: 700;
    line-height: 14px;
    color: rgb(7,17,27);
    margin: 18px 0 8px 0;
}
.food .food-content .content .detail{
     font-size: 10px;
     line-height: 10px;
     color: rgb(147,153,159);
     margin-bottom: 9px;
}
.food .food-content .content .detail .sell-count{
     padding-right: 12px;
}
.food .food-content .content .price{
      margin-bottom: 9px;
}
.food .food-content .content .price .now{
     font-size: 14px;
     font-weight: 700;
     color: rgb(240,20,20);
     line-height: 24px;
}
.food .food-content .content .price .old{
     font-size: 10px;
     font-weight:normal;
     color: rgb(147,153,159);
     line-height: 24px;
}
.food .food-content .content .cartcontrol-wrapper{
     position: absolute;
     right: 28px;
     bottom: 10px;
}
.food .food-content .content .buy{
     font-size: 10px;
     color: rgb(255,255,255);
     line-height: 12px;
     border-radius: 12px;
     background: rgb(0,160,220);
     padding: 6px 12px;
     position: absolute;
     right: 18px;
     bottom: 10px;
}
.food .food-content .info{
     padding: 18px;
     border-bottom: 1px solid rgba(7,17,27,.1);
}
.food .food-content .info .info-title{
     font-size: 14px;
     color: rgb(7,17,27);
     line-height: 14px;
     font-weight: 500;
}
.food .food-content .info .info-txt{
     padding: 6px 8px 0;
     font-size: 12px;
     font-weight: 200;
     line-height: 24px;
     color: rgb(77,85,93);
}

/*  动画*/
.move-enter{
    opacity: 0;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
}
.move-enter-active{
    transition: all .5s;
}
.move-leave-active{
    transition: all .5s;
    opacity: 0;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
}



